

import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean } from 'tds-ui/shared/utility';
@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: '[tds-rate-item]',
  exportAs: 'tdsRateItem',
  template: `
    <div
      class="tds-rate-star-second select-none text-current"
      (mouseover)="hoverRate(false); $event.stopPropagation()"
      (click)="clickRate(false)"
    >
      <ng-template
        [ngTemplateOutlet]="character || defaultCharacter"
        [ngTemplateOutletContext]="{ $implicit: index }"
      ></ng-template>
    </div>
    <div [ngClass]="{'text-base-amber-400 dark:text-d-base-amber-400 opacity-100':isHalf,'opacity-0 text-current':!isHalf}" class="tds-rate-star-first select-none  absolute left-0 top-0 w-1/2 h-full overflow-hidden " (mouseover)="hoverRate(true); $event.stopPropagation()" (click)="clickRate(true)">
      <ng-template
        [ngTemplateOutlet]="character || defaultCharacter"
        [ngTemplateOutletContext]="{ $implicit: index }"
      ></ng-template>
    </div>

    <ng-template #defaultCharacter>
      <i class="tdsi-star-1-fill"></i>
    </ng-template>
  `
})
export class TDSRateItemComponent {
  static ngAcceptInputType_allowHalf: BooleanInput;
  @Input() @InputBoolean() isHalf: boolean = false;
  @Input() character!: TemplateRef<void>;
  @Input() index = 0;
  @Input() @InputBoolean() allowHalf: boolean = false;
  @Output() readonly itemHover = new EventEmitter<boolean>();
  @Output() readonly itemClick = new EventEmitter<boolean>();

  hoverRate(isHalf: boolean): void {
    this.itemHover.next(isHalf && this.allowHalf);
  }

  clickRate(isHalf: boolean): void {
    this.itemClick.next(isHalf && this.allowHalf);
  }
}
