

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { LEFT_ARROW, RIGHT_ARROW } from '@angular/cdk/keycodes';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  Renderer2,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ButtonSize, NgClassType, TDSConfigKey, TDSConfigService, WithConfig } from "tds-ui/core/config";
import { InputBoolean, InputNumber } from 'tds-ui/shared/utility';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'rate';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-rate',
  exportAs: 'tdsRate',
  preserveWhitespaces: false,
  template: `
    <ul
      #ulElement
      class="tds-rate  box-border m-0 p-0 inline-block text-neutral-1-50 dark:text-d-neutral-1-200"
      [class.text-xl]="size == 'md'"
      [class.leading-5]="size == 'md'"
      [class.text-2xl]="size == 'lg'"
      [class.leading-6]="size == 'lg'"
      [class.text-base]="size == 'sm'"
      [class.leading-4]="size == 'sm'"
      [class.tds-rate-disabled]="disabled"
      [class.tds-rate-rtl]="dir === 'rtl'"
      [ngClass]="classMap"
      (blur)="onBlur($event)"
      (focus)="onFocus($event)"
      (keydown)="onKeyDown($event); $event.preventDefault()"
      (mouseleave)="onRateLeave(); $event.stopPropagation()"
      [tabindex]="disabled ? -1 : 1"
    >
      <li
        *ngFor="let star of starArray; let i = index"
        class="tds-rate-star relative inline-block"
       
        [ngClass]="starStyleArray[i] || ''"
        tds-tooltip
        [tooltipTitle]="tooltips[i]"
      >
        <div
          tds-rate-item
          [isHalf]="checkIsHalf(i+1)"
          [allowHalf]="allowHalf"
          [character]="character"
          [index]="i"
          (itemHover)="onItemHover(i, $event)"
          (itemClick)="onItemClick(i, $event)"
        ></div>
      </li>
    </ul>
  `,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TDSRateComponent),
      multi: true
    }
  ]
})
export class TDSRateComponent implements OnInit, OnDestroy, ControlValueAccessor, OnChanges {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;

  static ngAcceptInputType_allowClear: BooleanInput;
  static ngAcceptInputType_allowHalf: BooleanInput;
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_autoFocus: BooleanInput;
  static ngAcceptInputType_count: NumberInput;

  @ViewChild('ulElement', { static: false }) private ulElement?: ElementRef;


  @Input() @WithConfig() @InputBoolean() allowClear: boolean = true;
  @Input() @WithConfig() @InputBoolean() allowHalf: boolean = false;
  @Input() @InputBoolean() disabled: boolean = false;
  @Input() @InputBoolean() autoFocus: boolean = false;
  @Input() character!: TemplateRef<void>;
  @Input() @InputNumber() count: number = 5;
  @Input() tooltips: string[] = [];
  @Output() readonly tdsOnBlur = new EventEmitter<FocusEvent>();
  @Output() readonly tdsOnFocus = new EventEmitter<FocusEvent>();
  @Output() readonly tdsOnHoverChange = new EventEmitter<number>();
  @Output() readonly tdsOnKeyDown = new EventEmitter<KeyboardEvent>();

  classMap: NgClassType = {};
  starArray: number[] = [];
  starStyleArray: NgClassType[] = [];
  dir: Direction = 'ltr';

  private readonly destroy$ = new Subject<void>();
  private hasHalf = false;
  private hoverValue = 0;
  private isFocused = false;
  private _value = 0;
  private _size: ButtonSize = "md";
  @Input() get size() {
    return this._size;
  };
  set size(size: ButtonSize) {
    this._size = size;
  };
  get value(): number {
    return this._value;
  }

  set value(input: number) {
    if (this._value === input) {
      return;
    }

    this._value = input;
    this.hasHalf = !Number.isInteger(input);
    this.hoverValue = Math.ceil(input);
  }

  constructor(
    public tdsConfigService: TDSConfigService,
    private renderer: Renderer2,
    private cdr: ChangeDetectorRef,
    @Optional() private directionality: Directionality
  ) { }

  ngOnChanges(changes: SimpleChanges): void {
    const { autoFocus, count, value } = changes;

    if (autoFocus && !autoFocus.isFirstChange()) {
      const el = this.ulElement!.nativeElement;
      if (this.autoFocus && !this.disabled) {
        this.renderer.setAttribute(el, 'autofocus', 'autofocus');
      } else {
        this.renderer.removeAttribute(el, 'autofocus');
      }
    }

    if (count) {
      this.updateStarArray();
    }

    if (value) {
      this.updateStarStyle();
    }
  }

  ngOnInit(): void {
    this.tdsConfigService
      .getConfigChangeEventForComponent(TDS_CONFIG_MODULE_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => this.cdr.markForCheck());

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onItemClick(index: number, isHalf: boolean): void {
    if (this.disabled) {
      return;
    }

    this.hoverValue = index + 1;

    const actualValue = isHalf ? index + 0.5 : index + 1;

    if (this.value === actualValue) {
      if (this.allowClear) {
        this.value = 0;
        this.onChange(this.value);
      }
    } else {
      this.value = actualValue;
      this.onChange(this.value);
    }

    this.updateStarStyle();
  }

  onItemHover(index: number, isHalf: boolean): void {
    if (this.disabled || (this.hoverValue === index + 1 && isHalf === this.hasHalf)) {
      return;
    }

    this.hoverValue = index + 1;
    this.hasHalf = isHalf;
    this.tdsOnHoverChange.emit(this.hoverValue);

    this.updateStarStyle();
  }

  onRateLeave(): void {
    this.hasHalf = !Number.isInteger(this.value);
    this.hoverValue = Math.ceil(this.value);

    this.updateStarStyle();
  }

  onFocus(e: FocusEvent): void {
    this.isFocused = true;
    this.tdsOnFocus.emit(e);
  }

  onBlur(e: FocusEvent): void {
    this.isFocused = false;
    this.tdsOnBlur.emit(e);
  }

  focus(): void {
    this.ulElement!.nativeElement.focus();
  }

  blur(): void {
    this.ulElement!.nativeElement.blur();
  }

  onKeyDown(e: KeyboardEvent): void {
    const oldVal = this.value;

    if (e.keyCode === RIGHT_ARROW && this.value < this.count) {
      this.value += this.allowHalf ? 0.5 : 1;
    } else if (e.keyCode === LEFT_ARROW && this.value > 0) {
      this.value -= this.allowHalf ? 0.5 : 1;
    }

    if (oldVal !== this.value) {
      this.onChange(this.value);
      this.tdsOnKeyDown.emit(e);
      this.updateStarStyle();
      this.cdr.markForCheck();
    }
  }

  private updateStarArray(): void {
    this.starArray = Array(this.count)
      .fill(0)
      .map((_, i) => i);

    this.updateStarStyle();
  }

  checkIsHalf(value: number) {
    return this.hasHalf && value === this.hoverValue
  }
  private updateStarStyle(): void {
    this.starStyleArray = this.starArray.map(i => {
      const prefix = 'tds-rate-star';
      const value = i + 1;
      return {
        [`text-base-amber-400 dark:text-d-base-amber-400`]: value < this.hoverValue || (!this.hasHalf && value === this.hoverValue),
        [`${prefix}-full`]: value < this.hoverValue || (!this.hasHalf && value === this.hoverValue),
        [`${prefix}-half`]: this.hasHalf && value === this.hoverValue,
        [`${prefix}-active`]: this.hasHalf && value === this.hoverValue,
        [`${prefix}-zero`]: value > this.hoverValue,
        [`${prefix}-focused`]: this.hasHalf && value === this.hoverValue && this.isFocused,
        ['mr-2']: this.size == "sm",
        ['mr-2.5']: this.size == "md",
        ['mr-3']: this.size == "lg",
        ['cursor-pointer']:!this.disabled,
        // ['cursor-not-allowed']:!this.disabled
      };
    });
  }

  writeValue(value: number | null): void {
    this.value = value || 0;
    this.updateStarArray();
    this.cdr.markForCheck();
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  registerOnChange(fn: (_: number) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  onChange: (value: number) => void = () => null;
  onTouched: () => void = () => null;
}
